
import { defineComponent, nextTick, onMounted, ref, watch } from "vue";
import { useAsyncTask } from "vue-concurrency";

import { API } from "@/api";
import ErrorMessage from "@/components/ErrorMessage.vue";
import Form from "@/components/job/FormWrapper.vue";
import Jobs from "@/components/job/Jobs.vue";
import Loading from "@/components/Loading.vue";
import { JobsWithPagination, SearchParams } from "@/types";

export default defineComponent({
  name: "JobsWrapper",
  components: {
    Loading,
    ErrorMessage,
    Form,
    Jobs,
  },
  setup() {
    const page = ref(1);
    const form = ref<InstanceType<typeof Form>>();

    const getJobsTask = useAsyncTask<JobsWithPagination, [SearchParams]>(
      async (_signal, params) => {
        return await API.getJobs(params);
      }
    );

    const getJobs = async () => {
      const params = form.value?.getSearchParams() as SearchParams;
      return await getJobsTask.perform(params);
    };

    const updatePage = (newPage: number) => {
      page.value = newPage;
    };

    const resetPage = () => {
      page.value = 1;
    };

    const refreshPage = async () => {
      resetPage();
      await getJobs();
    };

    const search = async () => {
      resetPage();
      nextTick(async () => await getJobs());
    };

    onMounted(async () => {
      await getJobs();
    });

    watch(page, async () => {
      nextTick(async () => await getJobs());
    });

    return {
      getJobsTask,
      page,
      form,
      search,
      refreshPage,
      updatePage,
    };
  },
});
